import React, { useEffect, useState } from "react";
import {
  Typography,
  Divider,
  Grid,
  TextField,
  Box,
  Card,
  CardContent,
} from "@mui/material";
import SummarizeIcon from "@mui/icons-material/Summarize";
import { CircularButton } from "../../components/buttons/CircularButton";
import { GridSearchIcon } from "@mui/x-data-grid";
import { useDispatch, useSelector } from "react-redux";
import { uiErrorMessage } from "../../redux/actions/ui.actions";
import { Loading } from "../../components/Loadings/Loading";
import ErrorAlert from "../../components/Alerts/ErrorAlert";
import { CleanBalance, GetBalance } from "../../redux/actions/prestamo.actions";

export const Balance = () => {
  const dispatch = useDispatch();
  const [fecha_ini, setFecha_ini] = useState("");
  const [fecha_fin, setFecha_fin] = useState("");
  const { balance } = useSelector((state) => state.prestamo);

  const handleChangeF1 = (e) => {
    e.preventDefault();
    setFecha_ini(e.target.value);
  };
  const handleChangeF2 = (e) => {
    e.preventDefault();
    setFecha_fin(e.target.value);
  };

  const handleSearch = () => {
    if (!fecha_ini)
      return dispatch(uiErrorMessage("Porfavor Seleccione un fecha de inicio"));
    if (!fecha_fin)
      return dispatch(uiErrorMessage("Porfavor Seleccione un fecha final"));
    dispatch(GetBalance(fecha_ini, fecha_fin));
  };

  useEffect(() => {
    return () => {
      dispatch(CleanBalance())
    }
  }, [])
  

  return (
    <>
      <div
        style={{
          display: "flex",
          justifyContent: "space-between",
          alignItems: "center",
        }}
      >
        <Typography variant="h6" gutterBottom>
          Balance
        </Typography>
        <SummarizeIcon />
      </div>
      <Divider />
      <Grid
        container
        spacing={1}
        sx={{ mt: 2 }}
        style={{
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
        }}
      >
        <Grid item xs={12} md={4}>
          <TextField
            type="date"
            label="Fecha Inicio"
            InputLabelProps={{ shrink: true }}
            fullWidth
            name="fecha_ini"
            value={fecha_ini}
            onChange={handleChangeF1}
          />
        </Grid>
        <Grid item xs={12} md={4}>
          <TextField
            fullWidth
            type="date"
            label="Fecha Fin"
            InputLabelProps={{ shrink: true }}
            name="fecha_fin"
            value={fecha_fin}
            onChange={handleChangeF2}
          />
        </Grid>
        <Grid
          item
          xs={12}
          md={4}
          style={{
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
          }}
        >
          <CircularButton
            type="button"
            text="Buscar"
            Icon={GridSearchIcon}
            handleClick={handleSearch}
          />
        </Grid>
      </Grid>
      {Object.entries(balance).length > 0 && (
        <Grid
          style={{ marginTop: 10 }}
          container
          spacing={3}
          direction={{ xs: "column", md: "row" }}
        >
          <Grid item xs={12} md={3}>
            <Card elevation={3}>
              <CardContent>
                <Typography variant="h6" component="div" gutterBottom>
                  Total Prestado
                </Typography>
                <Typography variant="h4" component="div">
                  ${balance?.totalPrestado.toLocaleString()}
                </Typography>
              </CardContent>
            </Card>
          </Grid>

          <Grid item xs={12} md={3}>
            <Card elevation={3}>
              <CardContent>
                <Typography variant="h6" component="div" gutterBottom>
                  Total Pagado
                </Typography>
                <Typography variant="h4" component="div">
                  ${balance?.totalPagado.toLocaleString()}
                </Typography>
              </CardContent>
            </Card>
          </Grid>

          <Grid item xs={12} md={3}>
            <Card elevation={3}>
              <CardContent>
                <Typography variant="h6" component="div" gutterBottom>
                  Total Capital
                </Typography>
                <Typography variant="h4" component="div">
                  ${balance?.totalCapital.toLocaleString()}
                </Typography>
              </CardContent>
            </Card>
          </Grid>

          <Grid item xs={12} md={3}>
            <Card elevation={3}>
              <CardContent>
                <Typography variant="h6" component="div" gutterBottom>
                  Total Interés Ganado
                </Typography>
                <Typography variant="h4" component="div">
                  ${balance?.totalInteresGanado.toLocaleString()}
                </Typography>
              </CardContent>
            </Card>
          </Grid>
        </Grid>
      )}

      <Loading />
      <ErrorAlert />
    </>
  );
};

export default Balance;
